h3 {
	font-weight: 600;
	font-size: 1.1rem;
	text-align: center;
	margin-bottom: 10px;
	}
.title {
	font-family: cursive ;
	font-weight: 700;
	}
p {
	font-family: Arial, Helvetica, "Roboto", sans-serif;
	font-size: 20px;
	line-height: 1.5;
	color: darkred;
	}
p.p2 , p.p4 , p.p3 {
	font-size: 18px;
	}
p.p3 {
	line-height: 1.3;
	}
.container {
	height: 100vh;
	width: 50vw;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: center;
	margin: auto;
	}
.card {
	perspective: 800px;
	transform-style: preserve-3d;
	margin-left: 10px;
	background-color: white;
	padding: 10px 20px 20px;
	max-width: 300px;
	min-height: 290px;
	transition: transform 0.2s ease;
	}
.card-item {
	background-color: #00D6FF ;
	width:250px;
	height:250px ;
	margin: auto;
	}
#setCardsBtn {
	margin-left: 46%;
	outline: none;
	}